import { Client } from 'pg'

const { PG_HOST, PG_PORT, PG_DATABASE, PG_USERNAME, PG_PASSWORD } = process.env;
const dbOptions = {
  host: PG_HOST,
  port: PG_PORT,
  database: PG_DATABASE,
  user: PG_USERNAME,
  password: PG_PASSWORD,
  ssl: {
    rejectUnauthorized: false,
  },
  connectionTimeoutMillis: 5000
}

export const getProductById = async (event) => {
  const client = new Client(dbOptions)
  await client.connect()

  const params = event.pathParameters || {};
  try {
    const { rows: product } = await client.query('select products.*, stocks.count FROM products LEFT JOIN stocks ON stocks.product_id=product_id where id = $1', [params.id]);
    return {
      statusCode: 200,
      body: JSON.stringify(product),
    };
  } catch (error) {
    console.error('Error durring database request executing:'.error)
  } finally {
    client.end()
  }

};
